import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: resolve => require(['../components/common/Home.vue'], resolve),
      children: [
        {
          path: '/',
          name: 'content',
          component: resolve => require(['../components/common/Content.vue'], resolve)
        },
        {
          path: '/article',
          name: 'article',
          component: resolve => require(['../components/page/Article.vue'], resolve)
        },
        {
          path: '/tool',
          name: 'tool',
          component: resolve => require(['../components/page/Tool.vue'], resolve)
        },
        {
          path: '/doc',
          name: 'doc',
          component: resolve => require(['../components/page/Doc.vue'], resolve)
        },
        {
          path: '/vedio',
          name: 'vedio',
          component: resolve => require(['../components/page/Vedio.vue'], resolve)
        },
        {
          path: '/project',
          name: 'project',
          component: resolve => require(['../components/page/Project.vue'], resolve)
        },
        {
          path: '/articleList',
          name: 'articleList',
          component: resolve => require(['../components/page/ArticleList.vue'], resolve)
        },
        {
          path: '/docList',
          name: 'docList',
          component: resolve => require(['../components/page/DocList.vue'], resolve)
        },
        {
          path: '/toolList',
          name: 'toolList',
          component: resolve => require(['../components/page/ToolList.vue'], resolve)
        },
        {
          path: '/projectList',
          name: 'projectList',
          component: resolve => require(['../components/page/ProjectList.vue'], resolve)
        },
        {
          path: '/vedioList',
          name: 'vedioList',
          component: resolve => require(['../components/page/VedioList.vue'], resolve)
        },
        {
          path: '/abort',
          name: 'abort',
          component: resolve => require(['../components/page/abort.vue'], resolve)
        }
      ]
    }
  ]
})



